import React from 'react';

const Modal = ({ onClose, currentPhoto }) => {
    const { name, description, category, url, github, index  } = currentPhoto;

    return (
        <div className="modalBackdrop">
            <div className="modalContainer">
            <h3 className="modalTitle">{name} </h3>
                <a href={url} target="_blank" rel="noreferrer">Check it Out!</a>
                <img src={require(`../../assets/large/${category}/${index}.jpg`).default} alt="current category" />
                <p>
                    {description}
                </p>
                <a href={github} target="_blank" rel="noreferrer">Github Repository</a>
                <br></br>
                <button type="button" onClick={onClose}>
                Close this modal
                </button>
            </div>
        </div>
    );
};
  
export default Modal;
  